.facts {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  width: 100%;
  margin: 238px 0 0 0;
  padding: 152px 0 132px 0;
  background-color: $bg_first_color;

  @media (max-width: $tablet_max) {
    margin: -1px 0 0 0;
    padding: 70px 0 50px 0;
  }

  @media (max-width: $mobile_max) {
    padding: 70px 0 77px 0;
  }
}

.facts__heading h2 {
  margin: 0;
  font-size: 30px;
  line-height: 30px;
  font-weight: 600;
  text-align: center;
  color: $text_fourth_color;

  @media (max-width: $tablet_max) {
    font-size: 24px;
    line-height: 41px;
  }
}

.facts__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  width: 84.5vw;
  max-width: 1216px;
  margin: 48px 0 0 0;
  padding: 0;
  list-style: none;

  @media (max-width: $tablet_max) {
    width: 72.5vw;
    min-height: 325px;
    margin: 11px 0 0 0;
  }

  @media (max-width: $mobile_max) {
    flex-direction: column;
    width: 87.5vw;
    min-height: 597px;
    margin: 32px 0 0 0;
  }
}

.facts__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  width: 263px;
  min-height: 122px;
  margin: 0 auto 20px auto;

  @media (max-width: $tablet_max) {
    margin: 0;
  }

  @media (max-width: $mobile_max) {
    width: 87.5vw;
    min-height: 116px;
  }
}

.facts__item-number {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.facts__item-number span {
  font-size: 28px;
  line-height: 38px;
  font-weight: 700;
  text-align: center;
  color: $button_color_form;
}

.facts__item-text p {
  margin: 0;
  font-size: 22px;
  line-height: 34px;
  font-weight: 400;
  text-align: center;

  @media (max-width: $mobile_max) {
    font-size: 18px;
  }
}
